@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.landing {
    text-align: center;
  }
  
  .logo,
  .logo2 {
    height: 20vmin;
    pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .logo {
      -webkit-animation: logo-spin-cw infinite 20s linear;
              animation: logo-spin-cw infinite 20s linear;
    }
    .logo2 {
      -webkit-animation: logo-spin-acw infinite 20s linear;
              animation: logo-spin-acw infinite 20s linear;
    }
  }
  
  .landing-header {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: whitesmoke;
  }
  
  @-webkit-keyframes logo-spin-cw {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  
  @keyframes logo-spin-cw {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  @-webkit-keyframes logo-spin-acw {
    from {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
    to {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
  }
  @keyframes logo-spin-acw {
    from {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
    to {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
  }
  
.about-page {
    height: 100vh;
    color: whitesmoke;
}
.page {
    height: 100vh;
    color: white;
    margin-top: 50px;
}

a {
    text-decoration: none;
    color: rgb(79, 141, 141);
}
.contact-page {
    height: 100vh;
    color: whitesmoke
}
